import { Lnb, CurrentBox, RadioBtn } from "../../components/bundle_components";

export default function NewsDetail() {
  return (
    <>
      <Lnb lnbType="board" />
      <CurrentBox mod={true} del={true} down={true} tit="FAQ " />
      <div className="faq_detail box_ty01 view_form">
        <div className="write_type">
          <div className="wirte_area">
            <div className="flex_box">
              <div className="input_ty02 flex_left">
                <label htmlFor=""></label>
                <input type="text" defaultValue={""} readOnly/>
              </div>
              <div className="flex_right">
                <label htmlFor=""></label>
                <div className="radio_group d-flex w100">
                  <RadioBtn for="show" id="show" name="show" text="" />
                  <RadioBtn for="noshow" id="noshow" name="show" text="" />
                </div>
              </div>             
            </div>
            <div className="flex_box">
              <div className="input_ty02 flex_left">
                <label htmlFor=""></label>
                <input type="text" defaultValue={"  "} readOnly/>
              </div>
              <div className="input_ty02 flex_right">
                <label htmlFor=""></label>
                <input type="text" placeholder="" defaultValue={"2023.05.08"} readOnly/>
              </div>
            </div>
            <div className="flex_box">
              <div className="input_ty02 flex_left w100">
                <label htmlFor=""></label>
                <textarea className="textarea" defaultValue={"           "} readOnly></textarea>
              </div>      
            </div>
            <div className="flex_box">
              <div className="flex_left w100">
                <label htmlFor=""></label>
                <div className="file_box input_ty02">
                  <div className="row">
                    <input type="text" defaultValue={"  1.jpg"} readOnly />
                    {/* <button type="button" className="btn_close">&times;</button> */}
                  </div>
                </div>
              </div>
            </div>            
            <div className="flex_box">
              <div className="input_ty02 flex_left w100">
                <label htmlFor=""></label>
                <textarea className="textarea" readOnly></textarea>
              </div>
            </div>
          </div>         
        </div>
      </div>
    </>
  );
}