import { useState } from "react";
import Radio from "../../components/RadioBtn";
import img from "../../assets/img/img.png";
import { Lnb, CurrentBox, FindAddr } from "../../components/bundle_components";
import { useEssentialInfo, useSelectInfo, useSelectBox, useUploadFile } from "../../hooks/bundle_hooks";
import { useNavigate } from "react-router-dom";

export default function UserAdd() {
  const allowType = ["jpg", "jpeg", "png", "gif"];
  const { form, setForm, valid, dupCheck, validPass, errorCheck } = useEssentialInfo();
  const { choiceForm, setChoiceForm, dataSel } = useSelectInfo();
  const { selectedValues, selecBoxHtml } = useSelectBox({
    transSel: [{ placeholder: "" }, "", "", "", "", "", "", ""],
    carSel: [{ placeholder: "" }, "", "", "", "", "", "", ""],
    oilSel: [{ placeholder: "" }, "", "", "", "LPG", "", ""],
    jobSel: [{ placeholder: "" }, "", "", "", "", "", ""],
    methodSel: [{ placeholder: "" }, "", "  ", "  ", "     "],
    abilitySel: [{ placeholder: "" }, "", "", "", "", "", "", " "],
  });
  const { fileData, deleteFile, uploadFile } = useUploadFile(allowType, 1, 1);
  const [addrBox, setAddrBox] = useState("");

  console.log(choiceForm);

  const dataSubmit = () => {
    const formData = new FormData();
    fileData.forEach(el => {
      formData.append("file", el.file);
    });
    let joinData = {};
    for (let prop in form) {
      joinData[prop] = form[prop].val;
    }
    joinData = { ...joinData, ...choiceForm, profile_picture: formData };
    console.log(joinData);
  };

  const history = useNavigate();

  return (
    <>
      <Lnb lnbType="user" />
      <CurrentBox can={true} add={true} tit="  " />
      <div className="user_add box_ty01">
        <div className="write_type">
          <div className="essential_area">
            <h4 className="write_tit"></h4>
            <div className="wirte_area">
              <div className="flex_box">
                <div className="input_ty02 flex_left">
                  <label htmlFor=""></label>
                  <div className="d-flex ip_box id_wrap">
                    <input type="text" placeholder="" value={form.id.val} data-type="id" onChange={valid} />
                    <button type="button" className="btn_ty01 btn_search" onClick={dupCheck}>
                      
                    </button>
                    {errorCheck("id_dup")?.alert}
                  </div>
                </div>

                <div className="input_ty02 flex_right">
                  <label htmlFor=""></label>
                  <div className="d-flex ip_box">
                    <input type="password" placeholder="" value={form.pw.val} data-type="pw" onChange={valid} />
                    {errorCheck("pw")?.alert}
                  </div>
                </div>
              </div>
              <div className="flex_box">
                <div className="input_ty02 flex_left">
                  <label htmlFor=""></label>
                  <div className="d-flex ip_box">
                    <input type="text" placeholder="" value={form.name.val} data-type="name" onChange={valid} />
                    {errorCheck("name")?.alert}
                  </div>
                </div>
                <div className="radio_group flex_right gender">
                  {/* <span className="label"></span> */}
                  <label htmlFor=""></label>
                  <div className="radio_wrap">
                    {[
                      ["", "male"],
                      ["", "female"],
                    ].map((el, idx) => {
                      return (
                        <Radio
                          key={idx}
                          for={el[1]}
                          id={el[1]}
                          name="gender"
                          text={el[0]}
                          dataType="gender"
                          dataValue={el[0]}
                          onClick={valid}
                          checked={form.gender.val === el[0] && true}
                        />
                      );
                    })}
                  </div>
                </div>
              </div>
              <div className="flex_box">
                <div className="input_ty02 flex_left">
                  <label htmlFor=""></label>
                  <div className="d-flex ip_box">
                    <input type="text" placeholder="" data-type="birth" value={form.birth.val} onChange={valid} />
                    {errorCheck("birth")?.alert}
                  </div>
                </div>
                <div className="input_ty02 flex_right">
                  <label htmlFor=""> </label>
                  <div className="d-flex ip_box">
                    <input type="text" placeholder="" data-type="family" value={form.family.val} onChange={valid} />
                    {errorCheck("family")?.alert}
                  </div>
                </div>
              </div>
              <div className="flex_box">
                <div className="input_ty02 flex_left">
                  <label htmlFor=""></label>
                  <div className="d-flex ip_box">
                    <input type="email" placeholder="" data-type="email" value={form.email.val} onChange={valid} />
                    {errorCheck("email")?.alert}
                  </div>
                </div>
                <div className="input_ty02 flex_right">
                  <label htmlFor=""> </label>
                  <div className="d-flex ip_box ">
                    <input type="text" placeholder="" data-type="ph" value={form.ph.val} onChange={valid} />
                    {errorCheck("ph")?.alert}
                  </div>
                </div>
              </div>
            </div>
          </div>

          <div className="select_area">
            <h4 className="write_tit"></h4>
            <div className="wirte_area">
              <div className="flex_box">
                <div className="input_ty02 flex_left">
                  {/* <span className="label"> </span> */}
                  <label htmlFor=""> </label>
                  <div className="file_wrap">
                    <button className="btn_file_add">
                      <label htmlFor="file">
                        {/* <span className="plus">&times;</span> */}
                        <span className="file-plus"></span>
                        <p></p>
                        <input type="file" id="file" onChange={uploadFile} />
                      </label>
                    </button>
                    {fileData[0] && (
                      <div className="profile_img">
                        <img src={fileData[0]?.url || img} alt="" />
                        <div className="hover">
                          <span className="file_name">{fileData[0]?.file.name}</span>
                          <button className="btn_del" data-url={fileData[0]?.url} onClick={deleteFile}>
                             
                          </button>
                        </div>
                      </div>
                    )}
                  </div>
                </div>
                <div className="row">
                  <div className="input_ty02 flex_right">
                    {/* <span className="label"> </span> */}
                    <label htmlFor=""> </label>
                    {selecBoxHtml[0]}
                  </div>
                  <div className="input_ty02 flex_right">
                    {/* <span className="label"></span> */}
                    <label htmlFor=""></label>
                    {selecBoxHtml[1]}
                  </div>
                </div>
              </div>
              <div className="flex_box">
                <div className="input_ty02 flex_left">
                  <label htmlFor=""></label>
                  <div className="addr_wrap">
                    <input type="text" placeholder="" value={choiceForm.addr} />
                    <button
                      type="button"
                      className="btn_ty01 btn_search"
                      onClick={() => {
                        setAddrBox("active");
                      }}
                    >
                      
                    </button>
                  </div>
                </div>
                <div className="input_ty02 flex_right">
                  <label htmlFor=""> (CC)</label>
                  <input type="text" placeholder="" data-type="how_cc" value={choiceForm.how_cc} onChange={dataSel} />
                </div>
              </div>
              <div className="flex_box">
                <div className="input_ty02 flex_left">
                  <label htmlFor=""></label>
                  <input type="text" placeholder="" data-type="addr_detail" value={choiceForm.addr_detail} onChange={dataSel} />
                </div>
                <div className="input_ty02 flex_right">
                  {/* <span className="label"></span> */}
                  <label htmlFor=""></label>
                  {selecBoxHtml[2]}
                </div>
              </div>
              <div className="flex_box">
                <div className="input_ty02 flex_left">
                  <label htmlFor=""></label>
                  <input type="text" placeholder=", , " />
                </div>
                <div className="radio_group flex_right hands">
                  {/* <span className="label">   </span> */}
                  <label htmlFor="">   </label>
                  <div className="radio_wrap">
                    {[
                      ["", "yes"],
                      ["", "no"],
                    ].map((el, idx) => {
                      return (
                        <Radio key={idx} for={el[1]} id={el[1]} name="have" text={el[0]} dataType="is_have" dataValue={el[0]} onClick={dataSel} />
                      );
                    })}
                  </div>
                </div>
              </div>
              <div className="flex_box">
                <div className="input_ty02 flex_left">
                  <label htmlFor=""></label>
                  {/* <span className="label"></span> */}
                  {selecBoxHtml[3]}
                </div>
                <div className="input_ty02 flex_right">
                  {/* <span className="label">   </span> */}
                  <label htmlFor="">   </label>
                  {selecBoxHtml[4]}
                </div>
              </div>
              <div className="flex_box">
                <div className="input_ty02 flex_left">
                  <label htmlFor=""></label>
                  <input type="text" placeholder="" defaultValue={"2023.05.30"} />
                </div>
                <div className="input_ty02 flex_right">
                  {/* <span className="label"></span> */}
                  <label htmlFor=""></label>
                  {selecBoxHtml[5]}
                </div>
              </div>
              <div className="flex_box">
                <div className="radio_group flex_left account">
                  <span className="label"> </span>
                  <div className="radio_wrap account">
                    {[
                      ["", "active"],
                      ["", "noActive"],
                    ].map((el, idx) => {
                      return (
                        <Radio
                          key={idx}
                          for={el[1]}
                          id={el[1]}
                          name="active"
                          text={el[0]}
                          dataType="active"
                          dataValue={el[0]}
                          onClick={dataSel}
                          checked={el[0] === choiceForm["active"] ? true : false}
                        />
                      );
                    })}
                  </div>
                </div>
                <div className="radio_group flex_right marry">
                  {/* <span className="label"></span> */}
                  <label htmlFor=""></label>
                  <div className="radio_wrap hands">
                    {[
                      ["", "married"],
                      ["", "single"],
                    ].map((el, idx) => {
                      return (
                        <Radio key={idx} for={el[1]} id={el[1]} name="marry" text={el[0]} dataType="is_married" dataValue={el[0]} onClick={dataSel} />
                      );
                    })}
                  </div>
                </div>
              </div>
              <div className="input_ty02 flex_box">
                <label htmlFor=""></label>
                <textarea placeholder="" data-type="comment" onChange={dataSel}></textarea>
              </div>
            </div>
          </div>
        </div>
        <div className="bottom_btn_wrap">
          <button type="button" className="btn_ty01 cancel" onClick={() => history(-1)}>
            
          </button>
          <button
            type="button"
            className="btn_ty01"
            disabled={!validPass()}
            onClick={() => {
              alert("");
            }}
          >
            
          </button>
        </div>
      </div>
      <FindAddr addrBox={addrBox} setAddrBox={setAddrBox} choiceForm={choiceForm} setChoiceForm={setChoiceForm} />
    </>
  );
}
