import { Lnb, CurrentBox, CheckBox } from "../../components/bundle_components";
import { useSelectBox, useDatePicker } from "../../hooks/bundle_hooks";

export default function Point() {
  const { date, startDate, endDate } = useDatePicker();
  const { selectedValues, selecBoxHtml } = useSelectBox({
    pay_state: ["", "", "", ""],
  });
  console.log(selectedValues);

  return (
    <>
      <Lnb lnbType="accumulated" />
      <CurrentBox add={true} del={true} down={true} tit=" " />
      <div className="point box_ty01 table_type table_comm accumulated">
        <div className="filter_wrap d-flex">
          <div className="select_input_wrap d-flex">{selecBoxHtml}</div>
          <div className="date_input_wrap d-flex">
            <div className="date_input input_ty02">{date.start}</div>
            <div className="date_input input_ty02">{date.end}</div>
          </div>
          <button type="button" className="btn_ty01 btn_search">
            
          </button>
        </div>
        <div className="table_wrap line">
          <table className="table">
            <colgroup>
              <col width={"auto"} />
              <col width={"80px"} />
              <col width={"130px"} />
              <col width={"230px"} />
              <col width={"250px"} />
              <col width={"180px"} />
              <col width={"320px"} />
              <col width={"150px"} />
              <col width={"200px"} />
            </colgroup>
            <thead>
              <tr>
                <th className="check">
                  <CheckBox for="wr_all" id="wr_all" name="wr_all" />
                </th>
                <th className="num">NO</th>
                <th></th>
                <th></th>
                <th>  </th>
                <th>  </th>
                <th> </th>
                <th> </th>
                <th className="etc"></th>
              </tr>
            </thead>
            <tbody>
              <AddPoint />
              <JoinPoint />
              <PromotionPoint />
            </tbody>
          </table>
        </div>
      </div>
    </>
  );
}

function AddPoint() {
  const { date, startDate, endDate } = useDatePicker();
  const { selectedValues, selecBoxHtml } = useSelectBox({
    pay_route: [{ placeholder: "" }, "", "", ""],
    pay_date: [
      { placeholder: "" },
      "  ",
      " n  ",
      " n  ",
      "    ",
      "    ",
      " n  ",
    ],
    pay_step: [{ placeholder: "" }, "", "", ""],
  });
  console.log(selectedValues);
  return (
    <tr>
      <td className="check">
        <CheckBox for="wr_1" id="wr_1" name="wr_1" />
      </td>
      <td className="num"></td>
      <td>
        <div className="select_input_wrap d-flex">{selecBoxHtml[0]}</div>
      </td>
      <td className="input_ty02">
        <input type="text" placeholder="" />
      </td>
      <td>{selecBoxHtml[1]}</td>
      <td>
        <div className="input_ty02 d-flex flex-ac point_input">
          <input type="text" placeholder="" /> P
        </div>
      </td>
      <td>
        <div className="date_input_wrap d-flex">
          <div className="date_input input_ty02">{date.start}</div>
          <span className="wave">~</span>
          <div className="date_input input_ty02">{date.end}</div>
        </div>
      </td>
      <td>
        <div className="select_input_wrap d-flex">{selecBoxHtml[2]}</div>
      </td>
      <td className="etc input_ty02">
        <input type="text" placeholder="" />
      </td>
    </tr>
  );
}

