import { Link, useLocation, useParams } from "react-router-dom";
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css";

export default function Lnb(props) {
  const { pathname } = useLocation();
  const { id } = useParams();
  const { lnbType } = props;

  const lnbTab = {
    user: [
      ["", "/UserList"],
      ["/", "/DeletedUserList"],
    ],
    userInfo: [
      [" ", "/UserBasicInfo/" + id],
      ["  ", "/UserPromoHis/" + id],
      ["  ", "/UserCalcHis/" + id],
      ["    ", "/UserRankHis/" + id],
      ["  ", "/UserStempHis/" + id],
      [" / ", "/UserPointHis/" + id],
      ["1:1 ", "/UserQnaHis/" + id],
      ["  ", "/UserDeviceHis/" + id],
    ],
    event: [
      ["  ", "/ChallengeList"],
      ["   ", "/RankingSetting"],
      ["   ", "/BannerRanking"],
      [" ", "/BannerSetting"],
      [" ", "/PopupSetting"],
    ],
    calcHistory: [["   ", "/CalculatorList"]],
    accumulated: [
      [" ", "/Stamp"],
      [" ", "/Point"],
    ],
    board: [
      ["/ ", "/News"],
      ["TIP  ", "/Tip"],
      ["FAQ ", "/Faq"],
      ["1:1 ", "/Qna"],
    ],
    device: [
      ["  ", "url"],
      ["GL  ", "/Product"],
    ],
    search: [
      ["", "/SearchPage"],
      ["", "/SearchPage/" + id],
      ["/ ", "/SearchPage/" + id],
      [" TIP ", "/SearchPage/" + id],
      ["FAQ  ", "/SearchPage/" + id],
      ["1:1 ", "/SearchPage/" + id],
    ],
  };

  const slideTab = {
    slidesPerView: "auto",
    spaceBetween: 10,
    slidesOffsetBefore: 20,
    slidesOffsetAfter: 20,
  };

  return (
    <div className="lnb container_item">
      <Swiper className="lnb_tab_slide" {...slideTab}>
        {lnbTab[lnbType].map((el, idx) => {
          return (
            <SwiperSlide key={idx} className={pathname.includes(el[1].replace(id, "")) && "active"}>
              <Link to={el[1]} className="lnb_btn">
                {el[0]}
              </Link>
            </SwiperSlide>
          );
        })}
      </Swiper>
    </div>
  );
}
