import { useNavigate } from "react-router-dom";
import { Lnb, CurrentBox, RadioBtn } from "../../components/bundle_components";
import { useSelectBox } from "../../hooks/bundle_hooks";

export default function NewsAdd() {
  const history = useNavigate();
  const { selectedValues, selecBoxHtml } = useSelectBox({
    faq_sort: ["", "", "", "", "", ""],
  });

  return (
    <>
      <Lnb lnbType="board" />
      <CurrentBox add={true} del={true} down={true} tit="FAQ /" />

      <div className="faq_add box_ty01 view_form add">
        <div className="write_type">
          <div className="wirte_area">
            <div className="flex_box">
              <div className="input_ty02 flex_left">
                <label htmlFor=""></label>
                {selecBoxHtml}
              </div>
              <div className="flex_right">
                <label htmlFor=""></label>
                <div className="radio_group d-flex w100">
                  <RadioBtn for="show" id="show" name="show" text="" />
                  <RadioBtn for="noshow" id="noshow" name="show" text="" />
                </div>
              </div>
            </div>
            <div className="flex_box">
              <div className="input_ty02 flex_left">
                <label htmlFor=""></label>
                <input type="text" defaultValue={"  "} />
              </div>
              <div className="input_ty02 flex_right">
                <label htmlFor=""></label>
                <input type="text" placeholder="" defaultValue={"2023.05.08"} />
              </div>
            </div>
            <div className="flex_box">
              <div className="input_ty02 flex_left w100 flex_box_mr">
                <span className="label"></span>
                <textarea
                  className="textarea faq_content"
                  placeholder=""
                  defaultValue={
                    "           "
                  }
                ></textarea>
              </div>
            </div>
            <div className="flex_box find_file">
              <div className="flex_left w100 flex_box_mr">
                <label htmlFor=""></label>
                {/* <button type="button" className="btn_plus"><span>&times;</span> </button> */}
                <div className="find_file_wrap">
                  {/* <input className="upload-name" defaultValue={""} placeholder=""/> */}
                  <label htmlFor="file" className="file_label">
                    <span></span>
                  </label>
                  <input type="file" className="file" />
                </div>
              </div>
            </div>
            <div className="file_box input_ty02 find_file">
              <div className="row">
                <input type="text" defaultValue={"  1.jpg"} readOnly />
                <button type="button" className="btn_close">
                  &times;
                </button>
              </div>
              <div className="row">
                <input type="text" defaultValue={"  1.jpg"} readOnly />
                <button type="button" className="btn_close">
                  &times;
                </button>
              </div>
            </div>
            <div className="flex_box">
              <div className="input_ty02 flex_left w100 flex_box_mr">
                <label htmlFor=""></label>
                <textarea className="textarea faq_note" placeholder=""></textarea>
              </div>
            </div>
          </div>
        </div>
        <div className="bottom_btn_wrap">
          <button type="button" className="btn_ty01 cancel" onClick={() => history(-1)}>
            
          </button>
          <button type="button" className="btn_ty01">
            
          </button>
        </div>
      </div>
    </>
  );
}
